<template>
  <section class="card-body p-0">
    <table class="table table-bordered border-primary m-0">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">PRODUCT</th>
          <th scope="col">Size</th>
          <th scope="col">PRICE</th>
          <th scope="col">QUANTITY</th>
          <th scope="col">SUB</th>
          <th scope="col">REMARK</th>
        </tr>
      </thead>
      <tbody class="table-group-divider">
        <template v-for="(item, index) in cartList.carts" :key="index">
          <tr class="luluText">
            <td>{{ index + 1 }}</td>
            <td>{{ item.product.title }}</td>
            <template v-for="(data, key) in item.option" :key="key">
              <template v-if="key === 0">
                <td>{{ data.size }}</td>
                <td>{{ key }}</td>
                <td>-3+</td>
                <td>$369</td>
                <td>@mdo</td>
              </template>
            </template>
          </tr>
          <tr class="luluText" v-if="item.option.length !==1">
            <td colspan="2"></td>
            <template v-for="(data, key) in item.option" :key="key">
              <template v-if="key !== 0">
                <td>{{ data.size }}</td>
                <td>{{ key }}</td>
                <td>-3+</td>
                <td>$369</td>
                <td>@mdo</td>
              </template>
            </template>
          </tr>
        </template>
        <tr>
          <td>total：</td>
          <td colspan="3">Larry the Bird</td>
          <td colspan="2"><button class="btn btn-outline-primary">Checkout</button></td>
        </tr>
      </tbody>
    </table>
  </section>
</template>

<script>
import emitter from '@/assets/javascript/emitter';

export default {
  data() {
    return {
      cartList: [],
    };
  },
  methods: {
    getCarts() {
      emitter.emit('loadingStatus', true);
      const api = `${process.env.VUE_APP_API}/carts`;
      this.$http
        .get(api)
        .then((res) => {
          this.cartList = res.data.data;
          emitter.emit('loadingStatus', false);
        })
        .catch((err) => {
          console.log(err);
          emitter.emit('loadingStatus', false);
        });
    },
  },
  mounted() {
    this.getCarts();
  },
};
</script>
